import React from "../node_modules/react";
import "./App.css";
import "./index.css";
import { BrowserRouter as Router , HashRouter} from "../node_modules/react-router-dom";
import Routes from "./Routes";

class App extends React.Component {

  render() {
    return (
     

      <Router>
      <div>
        <main>
        <HashRouter>
          <Routes />
          </HashRouter>
        </main>
      </div>
      </Router>
    

    );
  }
}
export default App;