import React from 'react';
import './App.css';
import AppNav from "./AppNav";
import { getWeb3, getInstance}  from "./Web3Util";
import backgroundImg from './bg.png';


var sectionStyle = {
  width: "100vw",
  paddingBottom:"150px",
  backgroundImage: `url(${backgroundImg})`,
  backgroundRepeat: 'no-repeat',
  backgroundSize: 'cover'
};

class ArtDetail extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      hasData: false,
      message: "",
      rows:[],
      columns: [],
      user: '',
      tokenIds: [],
      title: [],
      desc: [],
      price: [],
      publishDate: [],
      author: [],
      image: [],
      total: 0,
      contractInstance: '',
      user:'',
      selectedId:props.match.params.selectedId,
      art:null,
      priceInEther:0,
      owner:''
    };
    this.buyArt = this.buyArt.bind(this);
  }
  resetPendingArts() {
    this.setState({ 
      tokenIds: [],
      title: [],
      desc: [],
      price: [],
      publishDate: [],
      author: [],
      image: [],
      total: 0
    });
  }
  componentDidMount = async () => {
    const web3 = await getWeb3();
    window.web3 = web3;
    const contractInstance = await getInstance(web3);
    window.user = (await web3.eth.getAccounts())[0];
    this.setState({ user: window.user });
    this.setState({ contractInstance: contractInstance });
    await this.loadDigitalArts(web3);

  }
  async loadDigitalArts(web3) {
      try {
        const art = await this.state.contractInstance.methods.findArt(this.state.selectedId).call();
        const owner = await this.state.contractInstance.methods.ownerOf(this.state.selectedId).call();

        if(art != null){
          let avaxPrice =  web3.utils.fromWei(art[3], 'ether')
          this.setState({ hasData: true,art,avaxPrice,owner});
        } else {
          this.setState({ hasData: false });
        }
 
    } catch (e) {console.log('Error', e)}
  
  }
  async buyArt(tokenId, priceInEther) {
    try {
      const priceInWei =  window.web3.utils.toWei(priceInEther, 'ether');
      await this.state.contractInstance.methods.buyArt(tokenId).send({
          from: this.state.user, gas: 3000000, value: priceInWei
      })
      window.location.reload(); 
    } catch (e) {console.log('Error', e)}
  };

  render() {
    if (this.state.hasData) {

      return (
        <div className="App" style={sectionStyle}>
          <AppNav></AppNav>
          <section className="text-center">

          <div style={{color:"white"}}className="h1-responsive font-weight-bold text-center my-5">
              Creator: {this.state.art[7]}<br/>
              Current owner: {this.state.owner}<br/>
            </div>
       
            <div className="container">
                <div className="row" style={{marginTop:"50px"}}  >
                <div className="col-lg-4 col-md-12 mb-lg-0 mb-0"></div>
                    <div className="col-lg-4 col-md-12 mb-lg-0 mb-0">
                            <div style={{background:"white"}}>
                            <div className="view overlay rounded z-depth-3 mb-2">
                              <img className="img-fluid" src={this.state.art[9]} alt="Sample"/>
                            </div>
                            <h6 className="pink-text font-weight-bold mb-1"><i className="fas fa-map pr-2"></i></h6>
                            <div className="font-weight-bold orange-text deep-orange-lighter-hover">NFT unique ID: {this.state.art[0]}</div>
                            <h5 className="font-weight-bold mb-1">{this.state.art[1]}</h5>
                            <div className="dark-grey-text">Price: {this.state.avaxPrice} $AVAX</div>
                            <p style={{whiteSpace: 'nowrap',overflow: 'hidden',textOverflow:'ellipsis'}}>by <span className="font-weight-bold">{this.state.art[6]}</span>, {this.state.art[5]}</p>
                            
                            <p className="alert alert-primary dark-grey-text">{this.state.art[2]}</p>
                            {this.state.art[8] == this.state.owner && this.state.owner != this.state.user?
                              <button className="btn btn-pink btn-rounded btn-md" onClick={e => (e.preventDefault(),this.buyArt(this.state.art[0], this.state.avaxPrice))}>Buy</button>
                            :null}
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-12 mb-lg-0 mb-0"></div>

                </div>
              </div>
          </section>
        </div>
      );
    } else {
      return (
        <div className="App" style={sectionStyle}>
          <AppNav></AppNav>
          <section className="text-center">

            <div  style={{color:"white"}} className="h1-responsive font-weight-bold text-center my-5">
              Buy/Sell Digital Art on 420Swap
            </div>
            <p  style={{color:"white"}}  className="h1-responsive font-weight-bold text-center my-5">
            All the artworks on 420Swap are on the blockchain
            </p>
        
       
            
            <div className="container">
              <div className="alert alert-primary" role="alert">
                Publish your digital arts on the blockchain today!
              </div>
              </div>
          </section>
        </div>
      );
    }

  }
}

export default ArtDetail;
